.content {
    position: relative;
    margin-top: 100px;
  }
  
  .parallax-container {
    background-image: url("../../assets/images/home/parallax-background.png");
    position: relative;
    height: 800px;
    background-attachment: fixed;
    background-position: 25% 75%;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .background-image {
    width: 100%;
    height: auto;
  }
  
  .text-overlay {
    position: absolute;
    bottom: 15%;
    width: 500px;
    height: 200px;
    background-color: rgba(0, 0, 0, 0.4);
    color: #c9c9c9; /* Text color */
    justify-content: left;
    text-align: justify;
    padding: 10px 50px 10px 50px;
    margin: auto;
  }
  .text-overlay p {
    font-size: 21px;
  }
  
  /*information section*/
  .information-section {
    margin-bottom: 50px;
  }
  .information-section-grid {
    display: grid;
    grid-template-columns: 500px 500px;
    height: 100%;
    justify-content: space-evenly;
    justify-items: center;
    align-content: space-evenly;
    align-items: center;
    margin: 0px 220px 0px 220px;
  }
  .info-section-img {
    width: 400px;
    filter: drop-shadow(6px 0px 6px rgba(47, 47, 47, 0.5));
  }
  .info-section-text-div {
    margin-bottom: 10px;
  }
  ul {
    text-align: left;
  }
  
  .info-section-list {
    justify-content: left;
  }
  /*Brands we support section*/
  .brand-imgs-section {
    text-align: center;
    padding: 0px 50px 30px 50px;
    text-align: center;
    justify-content: center;
  }
  
  .brand-imgs {
    height: 30px;
    padding: 15px;
    flex-wrap: "hard";
    white-space: normal;
    display: inline-grid;
    place-items: center;
    margin: 0 auto;
  }
  #brand-img-hp {
    height: 50px;
    padding-bottom: 5px;
  }
  .button {
    text-decoration: none;
    color: #000;
  }
  .button:hover {
    color: #0c84f4;
  }
  
  /*Contact Page Link corner page curl*/
  #fpc_effect-back {
    height: 200px;
    background-color: #ffff; /* some background color to match corner inside's */
    justify-content: center;
    margin: 0 auto;
  }
  #fpc_effect-back * {
    box-sizing: border-box;
  }
  #fpc_box {
    width: 100%; /*any relative or absolute*/
    position: relative;
    background-color: #ffffff;
    color: #0c84f4;
  }
  #fpc_content {
    padding: 20px 20px 20px 180px;
  }
  #fpc_content:before {
    content: "";
    width: 160px;
    height: 160px;
    float: right;
  }
  #fpc_page-tip:before,
  #fpc_page-tip:after {
    background-color: #fff;
    position: absolute;
    display: block;
    z-index: 2;
    border-top-right-radius: 30%;
    width: 100%;
    height: 100%;
    content: "";
  }
  #fpc_page-tip:before {
    right: 100%;
    top: 0%;
    background: -webkit-radial-gradient(
      -180% 200%,
      circle,
      rgba(255, 255, 255, 0) 85%,
      rgba(0, 0, 0, 0.1) 93%
    );
  }
  #fpc_box:hover #fpc_page-tip:before {
    border-right: solid 1px #fff;
  }
  #fpc_box div#fpc_corner-box:hover #fpc_page-tip:before {
    border-right: solid 2px #fff;
  }
  #fpc_page-tip:after {
    top: 100%;
    right: 0%;
    background: -webkit-radial-gradient(
      -250% 320%,
      circle,
      rgba(255, 255, 255, 0) 85%,
      rgba(0, 0, 0, 0.1) 93%
    );
  }
  #fpc_box:hover #fpc_page-tip:after {
    border-top: solid 1px #fff;
  }
  #fpc_box div#fpc_corner-box:hover #fpc_page-tip:after {
    border-top: solid 2px #fff;
  }
  #fpc_corner-box {
    /* edit these sizes for the default revealing corner size */
    height: 40px;
    width: 40px;
    right: 0;
    top: 0;
    position: absolute;
    overflow: visible;
  }
  #fpc_box:hover #fpc_corner-box {
    /* edit corner size (First animation, when the whole page is rollovered) */
    height: 100px;
    width: 100px;
  }
  #fpc_box div#fpc_corner-box:hover {
    /* edit corner size (Second animation, when the corner itself is rollovered) */
    height: 100px;
    width: 100px;
  }
  #fpc_corner-box:before {
    position: absolute;
    top: 0;
    right: 0;
    content: "";
    display: block;
    width: 133%;
    height: 133%;
  }
  #fpc_corner-contents:after {
    position: absolute;
    top: 0;
    right: 0;
    content: "";
    background: -webkit-linear-gradient(
        45deg,
        rgba(255, 255, 255, 0) 37%,
        #ddd 62%,
        rgba(230, 230, 230, 0.1) 64%,
        rgba(255, 255, 255, 0) 67%
      ),
      -webkit-radial-gradient(-50% 150%, circle, transparent 74%, rgba(
              0,
              0,
              0,
              0.2
            )
            74%, transparent 81%);
    display: block;
    width: 133%;
    height: 133%;
  }
  #fpc_page-tip {
    position: absolute;
    top: 0;
    right: 0;
    content: "";
    background: -webkit-linear-gradient(
      45deg,
      #ddd 17%,
      #dfdfdf 18%,
      #f5f5f5 30%,
      #f8f8f8 34%,
      #eee 39%,
      rgba(200, 200, 200, 0) 41%
    );
    display: block;
    width: 100%;
    height: 100%;
  }
  #fpc_corner-button {
    position: absolute;
    width: 90px;
    height: 40px;
    top: 0;
    right: 0;
    background-color: #0c84f4;
    color: #fff;
    text-align: center;
    padding: 5px;
    border: none;
    font-size: 11px;
  }
  #fpc_corner-contents {
    width: 125%;
    position: absolute;
    display: block;
    overflow: hidden;
    -webkit-mask: -webkit-linear-gradient(45deg, transparent 49%, #000 53%);
    top: 0;
    right: 0;
    height: 125%;
  }
  #fpc_corner-contents:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: #eeeef4; /* Match this background color to #fpc_effect-back */
  }
  #fpc_corner-box,
  #fpc_corner-contents,
  #fpc_page-tip {
    -webkit-transition-property: all;
    -webkit-transition-duration: 0.3s;
    -webkit-transition-timing-function: cubic-bezier(0, 0.35, 0.5, 1.7);
  }
  #fpc_corner-button strong {
    font-size: 13px;
    font-weight: bold;
    display: block;
  }
  /*Contact Page Link ^^^^ */
  
  @media (max-width: 505px) {
    .text-overlay {
      bottom: 5%;
      width: 200px;
      height: 100px;
      background-color: rgba(0, 0, 0, 0.4);
      padding: 5px 25px 5px 25px;
    }
    .text-overlay p {
      font-size: 11px;
    }
    .parallax-container {
      height: 300px;
      background-position: top;
      background-size: auto 400px;
    }
  
    .brand-imgs {
      height: 15px;
      padding: 5px;
    }
    #brand-img-hp {
      height: 25px;
    }
    .brand-imgs-section {
      text-align: center;
      padding: 0px 30px 20px 30px;
      text-align: center;
      justify-content: center;
    }
    #fpc_effect-back {
      height: auto;
      background-color: #ffffff; /* some background color to match corner inside's */
      width: 100%; /* trivial */
    }
    #fpc_content {
      padding: 20px 10px 10px 25%;
      font-size: 10px;
      margin: 0px;
      justify-content: center;
    }
    #fpc_content:before {
      width: 100px;
      height: 100px;
    }
    #fpc_content h1 {
      font-size: 20px;
    }
    #fpc_content h2 {
      font-size: 15px;
    }
    #fpc_box:hover #fpc_corner-box {
      /* edit corner size (First animation, when the whole page is rollovered) */
      height: 30px;
      width: 30px;
    }
    #fpc_box div#fpc_corner-box:hover {
      /* edit corner size (Second animation, when the corner itself is rollovered) */
      height: 60px;
      width: 60px;
    }
  
    .information-section-grid {
      grid-template-columns: 400px;
      justify-content: space-evenly;
      justify-items: center;
      align-content: space-evenly;
      align-items: center;
      margin: auto;
    }
    .info-section-img {
      width: 300px;
    }
    .info-section-text-div {
      width: 300px;
      font-size: 14px;
    }
  }
  
    
    